@import url(https://fonts.googleapis.com/css?family=Ubuntu:400&display=swap);
body {
	margin: 0;
	font-family: "Ubuntu", "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
background: rgb(38,252,2);
background: -webkit-gradient(linear, left bottom, left top, from(rgba(38,252,2,1)), to(rgba(117,121,117,1)));
background: linear-gradient(0deg, rgba(38,252,2,1) 0%, rgba(117,121,117,1) 100%);
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
src: local('SEGA LOGO FONT'), url(/robo-friends/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1 {
    font-family: 'SEGA LOGO FONT';
    font-size: 80px;
    font-weight: 200;
    color: rgb(252, 232, 3);
}


